import { Injectable } from '@angular/core';

import { HttpClient } from '@angular/common/http'

// import observable related code
import { Observable } from "rxjs";
import { throwError } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class ListService {

 booksUrl= 'https://anapioficeandfire.com/api/books';
 characterUrl = 'https://anapioficeandfire.com/api/characters/'
 houseUrl = 'https://anapioficeandfire.com/api/houses'


  constructor(private _http:HttpClient) { 
    
  }

 
public  getBooks(): any{
    let response =  this._http.get(this.booksUrl);
    return response
  }

  public  getCharacters(): any{
    let response =  this._http.get(this.characterUrl);
    return response
  }

  public  getHouses(): any{
    let response =  this._http.get(this.houseUrl);
    return response
  }

  public getDetailInformation(url) :any{
    let response = this._http.get(url)
    return response
  }

}


