import { Component, OnInit } from '@angular/core';

import { ListService } from '../list.service'

import { MyfilterPipe } from '../myfilter.pipe';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {

  public allList = [];
  public books;
  houses;
  characters;
  term = 'all'


  constructor(public ListService: ListService) {

  }

  ngOnInit() {


    this.getBooksList();
    this.getCharacterList();
    this.getHouseList();

  }

  public getBooksList(): any {

    this.ListService.getBooks().subscribe(

      data => {
        this.books = data;
        this.books.sort(function (a, b) {
          var textA = a.name.toUpperCase();
          var textB = b.name.toUpperCase();
          return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
        });
        for (let i of this.books) {
          this.allList.push(i);
        }
      }, error => {
        console.log('some error occured')
        console.log(error.errormessage)
      }
    )
  }


  public getCharacterList(): any {
    this.ListService.getCharacters().subscribe(

      data => {
        this.characters = data
        this.characters.sort(function (a, b) {
          var textA = a.aliases[0].toUpperCase();
          var textB = b.aliases[0].toUpperCase();
          return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
        });
        for (let j of this.characters) {
          this.allList.push(j);

        }
      }, error => {
        console.log('some error occured')
        console.log(error.errormessage)
      }
    )
  }

  public getHouseList(): any {
    this.ListService.getHouses().subscribe(
      data => {
        this.houses = data
        this.houses.sort(function (a, b) {
          var textA = a.name.toUpperCase();
          var textB = b.name.toUpperCase();
          return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
        });
        for (let k of this.houses) {
          this.allList.push(k);
        }
      }, error => {
        console.log('some error occured')
        console.log(error.errormessage)
      }
    )
  }



}
















